// Basic setup. Most of the really basic stuff is included in "critical.scss".
@import "base/variables";

// Mixins, helpers and similar. "Helpers" are included in the critical file.
@import "utilities/functions";
@import "utilities/mixins";
@import "utilities/placeholders";
@import "utilities/animations";

// Layout is included in the critical file.
@import "vendor/swiper";

// All NON-CRITICAL components, since the critical ones should've been loaded using the inline styling already (see "critical.scss").
@import "components/noncritical/some-noncritical-component.scss";

body {
    background-color: #ebeff0;
}

@keyframes shake {

    10%,
    90% {
        transform: translate3d(-1px, 0, 0);
    }

    20%,
    80% {
        transform: translate3d(2px, 0, 0);
    }

    30%,
    50%,
    70% {
        transform: translate3d(-4px, 0, 0);
    }

    40%,
    60% {
        transform: translate3d(4px, 0, 0);
    }
}

.face-container {
    max-width: 250px;
    margin: auto;
    display: block;
    position: relative;

    .gyro {
        @include position(absolute, 0);
    }

    .face {
        position: relative;
    }

    .eye {
        height: 30px;
        width: 30px;
        background-color: black;
        border-radius: 50%;
        position: absolute;
        top: 65px;

        &--left {
            left: 52px;
        }

        &--right {
            right: 55px;
        }
    }
}

.swiper-container {
    opacity: 0;

    &.swiper-container-initialized {
        opacity: 1;
    }

    .swiper-slide {
        display: flex;
        flex-direction: row;
        justify-content: center;

        &.swiper-slide-active {

            .question,
            form .input-answer {
                transform: translateY(0);
                opacity: 1;
            }
        }

        .question {
            transition: transform 300ms ease-in-out, opacity 300ms ease-in-out;
            transition-delay: 100ms;
            transform: translateY(40px);
            opacity: 0;

            p {
                margin: 0 0 10px 0;
                font-size: 22px;
                color: #6b7480;
                font-family: Helvetica, sans-serif;
            }
        }

        form {
            .input-answer {
                display: flex;
                flex-direction: row;
                transition: transform 300ms ease-in-out, opacity 300ms ease-in-out;
                transition-delay: 100ms;
                transform: translateY(40px);
                opacity: 0;
            }

            input {
                border: 0;
                background-color: white;
                color: #6b7480;
                @include placeholder(#6b7480);
                letter-spacing: 1px;
                font-weight: bold;
                outline: none;
                padding-left: 10px;
                border-radius: 5px;
                height: 50px;
                width: 100%;

                &.shake {
                    animation-name: shake;
                    animation-duration: 500ms;
                    animation-fill-mode: both;
                }
            }

            button {
                outline: none;
                border: 0;
            }
        }

        .swiper-content {
            position: relative;
            width: 100%;
            padding: 20px 20px;
        }
    }
}
